
import { postPageData } from '@/service/main/system/system'
import { defineComponent, ref } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useStore } from '@/store'
import { Toast } from 'vant'
import { shareOptions } from '../config/share.config'

export default defineComponent({
  props: {
    topic: {
      type: Object,
      required: false
    }
  },
  emits: ['showShareCard'],
  setup(props, { emit }) {
    const isCollect = ref(false)
    const router = useRouter()
    const store = useStore()
    const route = useRoute()
    const userInfo = store.state.login.userInfo

    const checkTopicIsCollect = async () => {
      if (store.state.login.userInfo.mixin_uuid) {
        const ifCollect = await postPageData('/collect/check', {
          user_id: store.state.login.userInfo.mixin_uuid,
          tid: props.topic?.tid
        })
        if (ifCollect.data !== null) {
          isCollect.value = true
        }
      }
    }
    checkTopicIsCollect()

    const handleCollectClick = async () => {
      if (!userInfo.mixin_uuid) {
        store.dispatch('login/accountLoginAction', route.path)
      } else {
        // 用户登录了
        if (isCollect.value === true) {
          //删除收藏
          const isc = await postPageData('/collect/delete', {
            user_id: store.state.login.userInfo.mixin_uuid,
            tid: props.topic?.tid
          })
          if (isc.data !== null) {
            isCollect.value = false
          }
          Toast('删除收藏!!!')
        } else {
          //收擦干
          const isc = await postPageData('/collect/add', {
            user_id: store.state.login.userInfo.mixin_uuid,
            tid: props.topic?.tid
          })
          if (isc.data === null) {
            isCollect.value = true
          }
          Toast('收藏成功!!!')
        }
        store.dispatch('main/handleCollecTopictAction')
      }
    }

    const handleTopicClick = (tid: string) => {
      router.push(`/topic/${tid}`)
    }

    const handleShareClick = (tid: string) => {
      console.log('handleShareClick')
      emit('showShareCard', tid)
    }

    return {
      shareOptions,
      isCollect,
      handleCollectClick,
      handleTopicClick,
      handleShareClick
    }
  }
})
