
import { useStore } from '@/store'
import { computed, defineComponent, ref } from 'vue'
import SingleTopic from './cpns/single-topic.vue'
import { getPageListData } from '@/service/main/system/system'
import { Toast } from 'vant'
import { shareOptions } from './config/share.config'

export default defineComponent({
  components: {
    SingleTopic
  },
  props: {
    cid: {
      type: Number,
      required: false
    },
    name: {
      type: String,
      required: true
    }
  },
  setup(props) {
    const tidEmit = ref<string>()
    const store = useStore()
    const showShare = ref(false)
    const loading = ref(false)
    const finished = ref(false)
    const page = ref(0)
    const topicList = computed(() =>
      store.getters[`main/pageListData`](props.name)
    )
    const topicCount = computed(() =>
      store.getters[`main/pageListCount`](props.name)
    )

    const getTopicList = () => {
      let queryInfo = {
        offset: topicList.value.length,
        limit: 10
      }
      getPageListData(`/topic/${props.cid}`, queryInfo).then((res) => {
        if (topicCount.value === 0) {
          store.commit(
            `main/changeTopic${props.name}Count`,
            res.data?.totalCount
          )
          store.commit(`main/pushTopic${props.name}List`, res.data?.list)
          loading.value = false
          if (topicList.value?.length >= topicCount.value) {
            finished.value = true
          }
        } else if (topicList.value.length < topicCount.value) {
          store.commit(
            `main/changeTopic${props.name}Count`,
            res.data?.totalCount
          )
          store.commit(`main/pushTopic${props.name}List`, res.data?.list)
          loading.value = false
          if (topicList.value?.length >= topicCount.value) {
            finished.value = true
          }
        } else if (topicList.value.length === topicCount.value) {
          finished.value = true
        }
      })
    }

    const changeFinished = () => {
      // loading.value = true
      page.value = 0
      finished.value = false
    }

    const onSelectShare = (option: any) => {
      Toast(option.name)
      showShare.value = false
      const input = document.createElement('input')
      document.body.appendChild(input)
      input.setAttribute('readonly', 'readonly')
      input.setAttribute('value', `https://betxin.one/topic/${tidEmit.value}`)
      input.select()
      document.execCommand('copy')
      document.body.removeChild(input)
    }

    const showShareCard = (tid: string) => {
      showShare.value = true
      tidEmit.value = tid
    }

    return {
      showShare,
      topicList,
      finished,
      loading,
      getTopicList,
      changeFinished,
      onSelectShare,
      shareOptions,
      showShareCard
    }
  }
})
